import React, { Component } from 'react';
import './App.css';
import SpinnerClass from './components/spinner/SpinnerClass';

class App extends Component {
  state = {
    data: [],
    isLoading: true,
  };

  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/photos')
      .then(res => res.json())
      .then(data =>
        this.setState({
          data,
          isLoading: false,
        })
      );
  }
  render() {
    return (
      <div className="App">
        {this.state.isLoading ? (
          <SpinnerClass />
        ) : (
          this.state.data.map(img => <img key={img.id} src={img.thumbnailUrl} alt={img.title} />)
        )}
      </div>
    );
  }
}

export default App;
